
body {
  margin: 0 auto;
  padding: 0;
  min-width: 1024px;
  font-family: 'OpenSans', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  // color: $dark-blue;
  overflow-x: hidden;
 }

 img {
  max-width: 100%;
  height: auto;
 }

 a {
  text-decoration: none;
 }

 html {
  box-sizing: border-box;
 }

 *,
 *::before,
 *::after {
  box-sizing: inherit;
 }

 .visually-hidden:not(:focus):not(:active),
 input[type='checkbox'].visually-hidden,
 input[type='radio'].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
 }
